export const dateMixin = {
    data(){
        return {
            date: '',
            dateMenu: '',
            dateFormatted: '',
        }
    },
    watch: {
        // ============= DATE_WATCHER START =============
        date (val) {
            this.dateFormatted = this.formatDate(this.date)
        },
        // ============= DATE_WATCHER END =============
    },
    methods: {
        // ============= DATE_FUNCTIONS START =============
        formatDate(date) {
            if (!date) return null

            const [year, month, day] = date.split('-')
            return `${day}-${month}-${year}`
        },
        parseDate(date) {
            if (!date) return null

            const [month, day, year] = date.split('/')
            return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
        },
        // ============= DATE_FUNCTIONS END =============
    }
}




export const loaderMixin = {
    data(){
        return {
            loader: null,
            loading: false,
        }
    },
    watch: {
        // ============= LOADING_WATCHER START =============
        loader() {
            const l = this.loader
            this[l] = !this[l]

            setTimeout(() => (this[l] = false), 3000)

            this.loader = null
        },
        // ============= LOADING_WATCHER END =============


    },
}

