<template>
    <div class="login_container">
        <div class="left_page"></div>
        <div class="right_page">
            <ValidationObserver ref="formLogin" v-slot="{ handleSubmit }">
                <form @submit.prevent="handleSubmit(onSubmit)" class="login_form">


                <span class="login_form__title">
                    Tizimga kirish
                </span>


                    <ValidationProvider :rules="{required: true,
                    error: [errorStatus, errorMessage, errorPlace, 'email']}"
                                        v-slot="{ errors }" tag="div" class="input_checker">

                        <v-text-field outlined class="mt-5 login_form__input"
                                      :error-messages="errors" placeholder="Login kiriting"
                                      v-model="email">
                        </v-text-field>
                    </ValidationProvider>


                    <ValidationProvider :rules="{required: true,
                    error: [errorStatus, errorMessage, errorPlace, 'password']}"
                                        v-slot="{ errors }" tag="div" class="input_checker">

                        <v-text-field outlined class="login_form__input"
                                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                      :type="show1 ? 'text' : 'password'"
                                      counter
                                      @click:append="show1 = !show1"
                                      :error-messages="errors" placeholder="Parol kiriting"
                                      v-model="password">
                        </v-text-field>
                    </ValidationProvider>


                    <v-btn
                        class="primaryBtn login_form__btn"
                        elevation="0"
                        :loading="loading"
                        @click="loader = 'loading'"
                        type="submit"
                    >Kirish
                    </v-btn>


                    <!-- <div class="login_form__error">
                        Xatolik yuz berdimi? Login va parolizni unutdingizmi?
                        Iltimos biz bilan bog'laning! +998998442431
                    </div> -->
                </form>
            </ValidationObserver>
        </div>
    </div>
</template>

<script>
import {mapState, mapActions} from "vuex";
import {loaderMixin} from '@/mixins/dateLoaderMixins.js'
import api from 'axios'
import {BASE_URL} from '@/plugins/variables'
import {ability} from '@/plugins/abilities'

export default {
    mixins: [loaderMixin],
    data() {
        return {
            // ============= SECONDARY_DATA =============
            email: '',
            password: '',
            show1: false,
            // ============= SECONDARY_DATA =============


            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        email() {
            this.clearErrors()
        },
        password() {
            this.clearErrors()
        }
        // ============= OTHER_WATCHERS END =============
    },
    async mounted() {
        await this.refresh()
    },
    computed: {
        ...mapState(
            {
                permissions: state => state.main.permissions,
            }
        ),
    },
    methods: {
        ...mapActions(
            {
                postSignIn: 'main/postSignIn',
                fetchBeforeLogin: 'main/fetchBeforeLogin',
                fetchPermissions: 'main/fetchPermissions'
            }
        ),


        // ============= REFRESH START =============
        async refresh(search) {
            // await this.fetchMavjudIps({page: this.page, search: search})
        },
        // ============= REFRESH START =============


        // ============= CLEARS_AND_CLOSES START =============
        clearInputs() {
            this.email = ''
            this.password = ''
            this.clearErrors()
            this.$refs.formLogin.reset();
        },
        clearErrors() {
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        // ============= CLEARS_AND_CLOSES END =============


        // ============= ON_SUBMIT START =============
        async onSubmit() {
            this.$refs.formLogin.validate().then(async (success) => {
                if (success) {
                    // ============= IF SUCCESS =============


                    // ============= LOG_IN START =============
                    let beforeLogin = await api.get(BASE_URL + 'sanctum/csrf-cookie')


                    let resp = await this.postSignIn(
                        {
                            email: this.email,
                            password: this.password,
                        })

                    if (resp.response && resp.response.status == 422) {
                        if (resp.response.data.errors && resp.response.data.errors.email) {
                            this.errorMessage = resp.response.data.errors.email[0];
                            this.errorPlace = 'email'
                        } else if (resp.response.data.errors && resp.response.data.errors.password) {
                            this.errorMessage = resp.response.data.errors.password[0];
                            this.errorPlace = 'password'
                        } else if (resp.response.data.message) {
                            this.errorMessage = resp.response.data.message;
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    } else if (resp.response && resp.response.status == 409) {
                        if (resp.response.data.message) {
                            this.errorMessage = resp.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    } else {
                        await this.fetchPermissions(resp.data.role);
                        this.$router.push({name: 'home'});
                        this.clearInputs();
                    }

                    // ============= LOG_IN END =============


                } else {
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============

    }

}
</script>

